import React from "react";

import Css3Icon from "./images/css3.png";
import Html5Icon from "./images/HTML5_Logo_512.png";
import JavaScriptIcon from "./images/javascript_logo.png";
import SQLIcon from "./images/mysql.png";
import PhpIcon from "./images/php.svg";
import ReactIcon from "./images/reactIcon.png";
import SassIcon from "./images/sass.svg";
import VueIcon from "./images/vuejs.png";
import AngularIcon from "./images/angular.svg";

function TechStack() {
  return (
    <div className="tile is-child box">
      <h2 className="has-text-centered has-text-grey-darker is-size-6">
        Here are the technologies I am comfortable with:
      </h2>
      <br />
      <div style={techIconDivStyle}>
        <a
          href="https://developer.mozilla.org/en-US/docs/Web/Guide/HTML/HTML5"
          target="_blank"
          rel="noopener noreferrer"
        >
          <img className="techIcons slowPulse animated infinite pulse" src={Html5Icon} alt="HTML5 Icon" />
        </a>
        <a href="https://developer.mozilla.org/en-US/docs/Web/CSS/CSS3" target="_blank" rel="noopener noreferrer">
          <img className="techIcons slowPulse animated infinite pulse" src={Css3Icon} alt="CSS3 Icon" />
        </a>
        <a href="http://sass-lang.com/" target="_blank" rel="noopener noreferrer">
          <img src={SassIcon} alt="SASS Icon" className="techIcons slowPulse animated infinite pulse" />
        </a>
        <a href="https://developer.mozilla.org/en-US/docs/Web/JavaScript" target="_blank" rel="noopener noreferrer">
          <img src={JavaScriptIcon} alt="JavaScript Icon" className="techIcons slowPulse animated infinite pulse" />
        </a>
        <a href="https://vuejs.org/" target="_blank" rel="noopener noreferrer">
          <img src={VueIcon} alt="Vuejs Icon" className="techIcons slowPulse animated infinite pulse" />
        </a>

        <a href="https://angular.io/" target="_blank" rel="noopener noreferrer">
          <img src={AngularIcon} alt="PHP Icon" className="techIcons slowPulse animated infinite pulse" />
        </a>

        <a href="https://facebook.github.io/react/" target="_blank" rel="noopener noreferrer">
          <img src={ReactIcon} alt="Reactjs Icon" className="techIcons slowPulse animated infinite pulse" />
        </a>

        <a href="https://www.mysql.com/" target="_blank" rel="noopener noreferrer">
          <img src={SQLIcon} alt="mySQL Icon" className="techIcons slowPulse animated infinite pulse" />
        </a>
        <a href="http://php.net/manual/en/intro-whatis.php" target="_blank" rel="noopener noreferrer">
          <img src={PhpIcon} alt="PHP Icon" className="techIcons slowPulse animated infinite pulse" />
        </a>
      </div>
    </div>
  );
}

export default TechStack;

const techIconDivStyle = {
  alignItems: "center",
  display: "flex",
  justifyContent: "space-between"
};



