import React from "react";
import TechStack from "./TechStack";
import CenterMePic from "./images/portrait.jpg";
import "../style/InitialPage.css";

function Body() {
  return (
    <div className="animated fadeIn">
      <div className="tile is-ancestor">
        <div className="tile is-parent">
          <div className="tile is-child box animated fadeInUp slowerFadeIn">
            <div className="centerPictureAndText" style={PicAndTextStyle}>
              <figure style={CenterPictureStyle}>
                <img className="centerMePic is-square" src={CenterMePic} style={CenterPicStyle} alt="Johnathon" />
              </figure>
              <p style={AboutMeStyle}>
                {" "}
                I'm Johnathon and I currently live in Oregon. My mission is to provide great websites for awesome
                clients. I develop creative solutions for back-end and front-end problems. I enjoy building all kinds of
                websites; Full blown web applications or static websites just providing information. The websites I
                provide are responsive and secure. I use a range of technologies to make your web presence happen. I
                look forward to reading your business inquiries at my email{" "}
                <a href="mailto:johnathonsouthworth@gmail.com">southworthjohnathon@gmail.com</a>.{" "}
              </p>
            </div>
            <br />
            <p className="quote has-text-centered">
              <q>Luck is what happens when preparation meets opportunity.</q>
              <span> -Seneca</span>
            </p>
          </div>
        </div>
        <div className="tile is-4 slowestFadeIn is-vertical is-parent animated fadeInUp">
          <div className="tile is-child box">
            <p>
              I recently graduated college with a degree in web development. I worked to develop web applications for
              staff and students on the RCC campuses. One such project I worked on was to convert a legacy software
              application to a more modern web platform. I created this application to help track specific students in
              key building locations and generate reports for faculty. These reports helped RCC determine the FTE
              (full-time equivalent) for staff and ensured students were spending time in the particular labs that their
              classes required. This application has improved the quality of life for the staff of the college. Let's
              discuss what type of application I could build to improve your companies quality of life.
            </p>
          </div>
        </div>
      </div>
      <TechStack />
      <br />
    </div>
  );
}

const CenterPictureStyle = {
  display: "flex",
  flexGrow: 1,
  flexShrink: 0,
  justifyContent: "space-between"
};

const CenterPicStyle = {
  borderRadius: "8px"
};

const PicAndTextStyle = {
  display: "flex",
  justifyContent: "space-between"
};

const AboutMeStyle = {
  padding: "10px"
};

export default Body;



